body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overscroll-behavior: none;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
        
body { margin: 0; padding: 0; }
html, body, #app, div.app { height: 100%; }
#map {
	z-index: 1;
	position: absolute;
	left: 50px;
	right: 0px;
	top: 0px;
	bottom: 0px;
}


.ui.label.advisory {
	background-color: rgba(248, 220, 37, 1);
}
.ui.label.warning {
	background-color: rgba(246, 19, 19, 1);
	color: #f0f0f0;
}
.ui.label.emergency {
	background-color: rgba(105, 66, 179, 0.6);
	color: #f0f0f0;
}
